/* contact form css */
.container{
  width: 90%;
  margin: auto;
  overflow: hidden;
}

.contact-form{
  display: grid;
  grid-template-columns: auto auto;
   }

#contact-section{
  background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.9)),url(/FinalProject-Bootcamp-/static/media/contact.236001b7.jpg);
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100% ;
  padding-bottom: 2%;
}
#contact-section .container h2{
  text-align: center;
   text-decoration: underline;
    /* text-decoration-color:red; */
    text-underline-position: under;
    color: rgb(238, 235, 235);
    letter-spacing: 2px;
    
}
#contact-section .container p{
  text-align: center; 
  width: 70%; 
  margin-left: auto;
   margin-right: auto; 
   padding-bottom: 3%;
   color: #fff;
   letter-spacing:3px;
}

  .contact-input input{
  padding: 8px;
  margin:10px;
  margin-left: 15%;
  
  width: 70%;
  background-color:rgba(136, 133, 133, 0.5);
  color: white;
  border: none;
  outline:none;
}

.contact-input input::-webkit-input-placeholder {
  color: white;
}

.contact-input input:-ms-input-placeholder {
  color: white;
}

.contact-input input::-ms-input-placeholder {
  color: white;
}

.contact-input input::placeholder {
  color: white;
}
.contact-textarea textarea::-webkit-input-placeholder
{
  color: white;
}
.contact-textarea textarea:-ms-input-placeholder
{
  color: white;
}
.contact-textarea textarea::-ms-input-placeholder
{
  color: white;
}
.contact-textarea textarea::placeholder
{
  color: white;
}

.contact-textarea textarea{
padding: 10px;
margin: 10px;  
width: 70%;
margin-left: 15%;
background-color:rgba(136, 133, 133, 0.5);
color: white;
border: none;
outline:none;
}

